.top{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    margin-left: auto;
    margin-right: auto;
    max-width: 80rem; 
    padding-left: 1rem;
    padding-right: 1rem;
}

.welcome {
    margin-right: 5rem;
}

.contact-links {
    display: flex;
}

.contact-links a {
   margin-right: 1rem;
   cursor: pointer;
   margin-top: 1rem;
   
}

.contact-links a:hover {
   transform: scale(1.05);
 }

 .resume-wrapper {
    margin-top: 1.5rem;
    display: flex;
 }

 .resume {
    margin-top: 1rem;
    cursor: pointer;
    width: 40%;
 }

 .resume a {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    border-width: 1px;
    border-style: solid;
    border-color:  #2E8B57;
    /* border-radius: 0.375rem; */
    color:  #2E8B57;
    background: transparent;

   
 }

  .resume a:hover {
    background-color: #2E8B57;
    color: white;
    transition: 0.3s;
  }

