.luwa-photo > img {
    border-radius: 50%;
    /* animation: fade-in 1s ease-in-out; */
    display: none;
    margin-bottom: 4rem;
}

@media (min-width: 640px) {
    .top {
      padding-left: 1.5rem;
      padding-right: 1.5rem;
    }

    .welcome{
        text-align: center;
    }

    .welcome > h1 {
        font-size: 2.5rem;
    }

    .welcome h2 {
        font-size: 2rem;
    }
    
    .welcome > p {
        margin-top: 1.25rem;
        font-size: 1.125rem;
    }

    

    .resume-wrapper {
        margin-top: 2rem;
        margin-bottom: 2rem;
        justify-content: center;
     }

     .resume {
        margin-top: 0;
     }
    
     .contact-links {
        justify-content: center;
    }

    .luwa-photo > img {
        display: block;
        /* width: 350px;
        height: 350px; */
    }
  }

  @media (min-width: 768px) {
    .top{
      flex-direction: row;
      justify-content: space-between;
    }

    .welcome > h1 {
        font-size: 5rem;
    }

    .welcome h2 {
        margin-top: 0.5rem;
        font-size: 3rem;
    }

    .welcome > p {
        font-size: 1.5rem;
        line-height: 1.5;
    }

    .contact-links {
        justify-content: center;
    }

    .resume-wrapper {
        justify-content: center;
    }

    .resume a {
        padding-top: 1rem;
        padding-bottom: 1rem;
        font-size: 1.5rem;
        padding-left: 2.5rem;
        padding-right: 2.5rem;
    }
  }
  
  @media (min-width: 1024px) {
    .top {
      padding-left: 2rem;
      padding-right: 2rem;
    }

    .welcome {
        text-align: left;
    }

    .resume-wrapper {
        justify-content: flex-start;
     }

     .contact-links {
        justify-content: flex-start;
    }

    

  
  }

